@import 'normalize';
@import 'visuallyhidden';
@import 'variables';
@import 'mixins';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

img {
  max-width: 100%;
  display: block;
}

body {
  background-color: $bg-main;
  font-family: 'Segoe UI', Arial, Helvetica, sans-serif;
  color: $text-main;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.7;
}

.container {
  width: 100%;
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 10px;
}

.btn {
  font-family: 'Segoe UI', Arial, Helvetica, sans-serif;
  color: $text-main;
  font-size: 16px;
  font-weight: 300;
  border: none;
  line-height: 1;
  text-align: center;
  background: transparent;
  cursor: pointer;

  &:focus {
    outline: none;
  }
}

.link {
  transition: opacity .3s ease-in-out;

  &:hover {
    opacity: .6;
  }

  &:focus {
    outline: 1px solid $text-white;
  }

  &:active {
    outline: none;
  }
}

.email {
  color: $text-accent;
  white-space: nowrap;
  transition: opacity .3s ease-in-out;

  &:hover {
    opacity: .6;
  }

  &:focus {
    outline: 1px solid $text-accent;
  }

  &:active {
    outline: none;
  }
}

.bold {
  font-weight: 600;
}

.pre-line {
  white-space: pre-line;
}

.h2 {
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 600;
}

.row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.cell {
  padding: 0 20px;
}

.github {
  fill: $text-white;
}

.header {
  padding: 20px 0;
  background-color: $bg-dark;

  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  &__author {
    color: $text-white;
    line-height: 1;
  }

  &__right {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  &__link {
    line-height: 1;
    color: $text-white;
  
    &:not(:first-child) {
      margin-left: 30px;
    }
  
    &_contact {
      padding: 5px 10px 6px 10px;
      line-height: 1;
      border: 1px solid;
      border-color: rgba($text-white, .3);
      border-radius: 6px;
    }
  }
}

.cv {
  padding-top: 20px;
  padding-bottom: 50px;
}

.top {
  padding-bottom: 20px;
  margin-bottom: 70px;

  &__left {
    width: 50%;
  }

  &__right {
    width: 50%;
  }

  &__image {
    width: 100%;
    max-width: 400px;
    border-radius: 10px;
  }
}

.education {
  margin: 0 auto 70px auto;
  width: 100%;
  max-width: 1000px;
}

.skills {
  margin-bottom: 70px;

  &__heading {
    margin-top: 20px;
  }
}

.experience {

  &__top {
    padding-bottom: 20px;
    width: 100%;
  }
}

.portfolio {
  padding: 20px 0;

  &__row {
    width: 100%;
    margin-bottom: 20px;
  }

  &__top {
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__left {
    margin-bottom: 40px;
    width: 50%;
  }

  &__right {
    margin-bottom: 40px;
    width: 50%;
  }

  &__wrap {
    margin-bottom: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__image {
    display: inline-block;
    width: 100%;
    max-width: 560px;
    border-radius: 20px;
    border: 1px solid #ddd;
    transition: transform .15s ease-in-out;

    &:hover {
      transform: translateY(1px);
    }
  }

  &__page-link {

    &:focus {
      outline: 1px solid $text-accent;
    }
  }

  &__link {
    color: $text-accent;
    white-space: nowrap;

    &:focus {
      outline: 1px solid $text-accent;
    }
  }
}

.contact {
  padding-top: 20px;

  &__row {
    padding: 0 20px;
  }

  &__left {
    width: 50%;
  }

  &__form {
    padding: 20px;
    margin: 0 auto;
    max-width: 400px;
    width: 100%;
    border: 1px solid #ccc;
    border-bottom: none;
    border-radius: 10px 10px 0 0;
  }

  &__input {
    margin-bottom: 20px;
    padding: 0 20px;
    display: block;
    width: 100%;
    height: 40px;
    background-color: $bg-main;
    border: 1px solid #ccc;
    border-radius: 10px;
    color: $text-main;

    &.success {
      border-color: #91CEC6;
    }

    &::placeholder {
      color: $text-secondary;
    }

    &.success::placeholder {
      color: #91CEC6;
    }

    &:focus {
      border-color: $text-main;
      outline: none;
    }

    &_text {
      padding: 20px;
      min-height: 150px;
      max-height: 400px;
      resize: vertical;
      border-radius: 10px 10px 0 0;
    }
  }

  &__submit {
    padding: 10px 30px;
    margin: 0 auto;
    display: block;
    background-color: $btn-main;
    color: $text-white;
    border-radius: 50px;

    &.success {
      opacity: .5;
    }
  }

  &__right {
    width: 45%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-image: url(../img/stripe.svg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }

  &__text {
    color: $text-white;
    font-size: 48px;
  }
}

.footer {
  padding: 20px 0;
  background-color: $bg-dark;

  &__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  &__text {
    color: $text-white;
    line-height: 1;
  }

  &__right {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  &__link {
    line-height: 1;
    color: $text-white;
  
    &:not(:first-child) {
      margin-left: 30px;
    }
  }

  &__ontop {
    padding: 5px 10px 6px 10px;
    line-height: 1;
    border: 1px solid;
    border-color: rgba($text-white, .3);
    border-radius: 6px;
  }
}

@include tablet-landscape {

  .cv {
    padding-bottom: 20px;
  }

  .top {
    margin-bottom: 30px;
  }

  .skills {
    margin-bottom: 30px;
  }

  .portfolio__text {
    display: block;
  }
}

@include tablet-portrait {
  
  .row {
    flex-direction: column;
  }

  .cell {
    width: 100%;
  }

  .top__image {
    margin: 0 auto;
    max-width: 250px;
  }

  .portfolio {

    &__left {
      margin-bottom: 20px;
      text-align: center;
    }

    &__right {
      margin-bottom: 20px;
      text-align: center;
    }

    &_wrap {
      text-align: center;
    }
  }

  .contact__right {
    display: none;
  }
}

@include phone-medium {

  .cell {
    padding: 0;
  }

  .header__author {
    display: none;
  }

  .header__container {
    justify-content: center;
  }

  .footer__container {
    flex-direction: column;
    justify-content: center;
  }

  .footer__email-wrap {
    margin-bottom: 10px;
  }

  .contact__row {
    padding: 0;
  }
}

@include phone-small {

  .header__container {
    justify-content: center;
  }

  .footer__container {
    justify-content: center;
  }
}

